import React from 'react';
import SideBarRow from './sideBarRow';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import FiberNewIcon from '@material-ui/icons/FiberNew';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import ChatIcon from '@material-ui/icons/Chat';
import trophy from './trophy.js';
import "./leftSection.css";
import store from './store';
import {rfreshPost} from './api';



class leftSection extends React.Component{
    constructor(props){
        super(props);
        this.buttonActive=this.buttonActive.bind(this);
    }
    buttonActive(but){
        //console.log(but);
        store.dispatch({type:'postTypeChange',payload:but});
        if(!rfreshPost())
        return;
        let arr=[document.getElementById("sbr_new"),document.getElementById("sbr_popular"),document.getElementById("sbr_trending"),document.getElementById("sbr_me")];
        //console.log(but);
        arr.forEach(element => {
            element.style.backgroundColor="transparent";
        });
        switch(but.charAt(0)){
            case 'n':
                arr[0].style.backgroundColor="darkgrey";
            break;
            case 'p':
                arr[1].style.backgroundColor="darkgrey";
            break;
            case 't':
                arr[2].style.backgroundColor="darkgrey";
            break;
            case 'm':
                arr[3].style.backgroundColor="darkgrey";
            break;
            default:;
        }
    }
    componentDidMount(){
        document.getElementById("sbr_new").style.backgroundColor="darkgrey";
    }
    render(){
        return(
        <div class="leftSection">
            <div class="sidebar" >
                <SideBarRow title="New" Icon={FiberNewIcon} id="sbr_new" clickMe={()=>{this.buttonActive("new")}}/>
                <SideBarRow title="Popular" Icon={WhatshotIcon} id="sbr_popular" clickMe={()=>{this.buttonActive("popular")}}/>
                <SideBarRow title="Trending" Icon={TrendingUpIcon} id="sbr_trending" clickMe={()=>{this.buttonActive("trending")}}/>
                <SideBarRow title="My Posts" Icon={ChatIcon} id="sbr_me" clickMe={()=>{this.buttonActive("me")}}/>
            </div>
            <div class="leaderboard" >
                <SideBarRow title="Leader Board" Icon={trophy}/>
            </div>
        </div>
        );
    }
}

export default leftSection;